.example {
  color: #424242;
  max-width: 960px;
  background-color: #fff;
  padding: 16px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.example code {
  color: #fff;
  background-color: #009688;
  border-radius: 2px;
  padding: 2px 6px;
  font-family: monospace;
  font-size: 16px;
  font-weight: 700;
}

.example .background {
  height: 240px;
  background-image: url("demo.e3ba131c.jpg");
  background-position: center;
  background-size: cover;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: DM Sans;
}

.container {
  max-width: 480px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

.circle-btn {
  height: 18px;
  width: 18px;
  cursor: pointer;
  border: none;
  border-radius: 50%;
  transition: background-color .3s;
}

.circle-btn__arrow {
  stroke: #d41443;
  stroke-width: 4px;
}

.circle-btn:hover {
  background-color: #ecebeb;
}

.button {
  color: #fff;
  cursor: pointer;
  background-color: #d41443;
  border: none;
  border-radius: 2em;
  padding: .9em 1.7em;
  font-family: DM Sans;
  transition: background-color .3s;
}

.button:hover {
  background-color: #c7133e;
}

.button--reversed {
  color: #d41443;
  background-color: #fff;
}

.button--reversed:hover {
  background-color: #ecebeb;
}

.button--small {
  font-size: 8px;
}

.button--mid {
  font-size: 14px;
  font-weight: 700;
}

.button--big {
  font-size: 16px;
  font-weight: 700;
}

.button__chevron {
  stroke: currentColor;
  vertical-align: middle;
  margin-left: 13px;
}

@media screen and (min-width: 768px) {
  .button--mid {
    padding: .85em 2.3em;
    font-size: 16px;
  }
}

@media screen and (min-width: 1280px) {
  .button--small {
    padding: .85em 1.6em;
    font-size: 14px;
  }
}

